import * as React from "react";
import styled from "@emotion/styled";
import { colors } from "../../../design-system";

interface Item {
  id: string;
  color: string;
  category: string;
  label: string;
  value: string;
}

interface Props {
  items: Item[];
}

const Bar = styled.div(({ color, value }: any) => ({
  height: 20,
  width: value,
  transition: "color 200ms ease",
  background: color || colors.blue,
}));
const BarContainer = styled.div({
  height: 20,
  overflow: "hidden",
  borderRadius: 12,
});

const BarLine: React.FunctionComponent<Props> = ({ items }) => {
  return (
    <BarContainer className="flex">
      {items.map((item: Item) => (
        <Bar key={`bar-${item.id}`} {...item} />
      ))}
    </BarContainer>
  );
};

export default BarLine;
